<template>
	<view class="content">
		<view class="tab-bar">
			
			<view class="tab-left">
				<image src="../../static/img/a1.png" mode=""></image>
			</view>
			<view class="tab-center">
				<text>绿洲之恋</text>
			</view>
			<view class="tab-right">
				<view class="serch">
					<image src="../../static/image/search.png" mode=""></image>
				</view>
				<view class="add">
					<image src="../../static/image/add.png" mode=""></image>
				</view>
			</view>
		</view>
		
		<view class="main">
		     <view class="apply">
				 <view class="friend-list">
				 	<view class="friend-list-left">
				 		<text class="tip">1</text>
				 		<image src="../../static/image/添加用户.png"></image>
				 	</view>
				 	
				 	<view class="friend-list-right">
				 		<view class="top">
				 			<view class="name">好友申请</view>
				 			<view class="time">上午8:43</view>
				 		</view>
				 		
				 		<view class="text">加个好友呗！</view>
				 	</view>
				 </view>
			 </view>
		     <view class="friends">
		     	<view class="friend-list" v-for="(item,index) in friends":key="index">
		     		<view class="friend-list-left">
		     			<text class="tip">{{item.tip}}</text>
		     			<image src="../../static/defatlt.jpg"></image>
		     		</view>
		     		
		     		<view class="friend-list-right">
		     			<view class="top">
		     				<view class="name">{{item.name}}</view>
		     				<view class="time">{{item.time}}</view>
		     			</view>
		     			
		     			<view class="text">{{item.lastMessage}}</view>
						
		     		</view>
		     	</view>
		     </view>
		</view>
		
	</view>
</template>

<script>
	import datas from '../../common/js/datas.js'
	import myfun from '../../common/js/myfun.js'
	import {getHomeList} from '../../util/api.js'
	export default {
		data() {
			return {
				friends:[]
			}
		},
		onLoad() {
			this.getFriend()
		},
		methods: {
			changeTIme(data){
				return myfun.dateTime(data)
			},
			getFriend(){
				
				getHomeList().then((res)=>{
					if(res.data.status==1){
						this.friends = res.data.requests
						console.log(this.friends)
					}
					
				})
				
			}
		},
		components:{
			// Footer
		}
	}
</script>

<style lang="scss">
	 
	@import "../../commons/css/myhead/myhead.scss";
	
	.content{
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding-top: var(--status-bar-height);
		padding-bottom: $uni-spacing-col-base;
	}
	

	.main{
		width: 100%;
		padding: 104rpx $uni-spacing-col-base 0;
		
		
	}
	.friend-list{
		height: 96rpx;
		padding: 16rpx 0;
		&:active{
			background-color: $uni-bg-color-grey;
		}
		.friend-list-left{
			position: relative;
			padding-left: 20rpx;
			float: left;
			image{
				width: 96rpx;
				height: 96rpx;
				border-radius: $uni-border-radius-base;
				background-color: $uni-color-primary;
			}
			.tip{
				position: absolute;
				top: -6rpx;
				left: 88rpx;
				min-width: 36rpx;
				height: 36rpx;
				line-height: 36rpx;
				background: $uni-color-warning;
				border-radius: $uni-border-radius-circle;
				z-index: 8;
				font-size: $uni-font-size-sm;
				color: $uni-text-color-inverse;
				text-align: center;
			}
		}
		
		.friend-list-right{
			padding-left: 148rpx;
			.top{
				height: 50rpx;
				.name{
					float: left;
					 font-size: 36rpx;
					 color: $uni-text-color;
					 line-height: 50rpx;
					 font-weight: 400;
				}
				.time{
					float: right;
					font-size: $uni-font-size-sm;
					color: $uni-text-color-disable;
					line-height: 50rpx;
					padding-right: 20rpx;
				}
			}
			.text{
				font-size: $uni-font-size-base;
				color: $uni-text-color-grey;
				line-height: 40rpx;
				display: -webkit-box;
				-webkit-box-orient:vertical;
				-webkit-line-clamp:1;
				overflow: hidden;
			}
		}
		
	}
</style>
